<template>
    <div class="background" ref="background"></div>
</template>

<script>
import * as THREE from 'three'
import { GLTFLoader } from 'three/examples/jsm/loaders/GLTFLoader'
import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls'
import { gsap } from 'gsap'

export default {
  name: 'BackGround',
  data () {
    return {
    }
  },
  methods: {
    init () {
      const container = document.querySelector('.background')
      const fov = 30
      const aspect = container.clientWidth / container.clientHeight
      const near = 1
      const far = 500
      this.camera = new THREE.PerspectiveCamera(fov, aspect, near, far)

      this.scene = new THREE.Scene()
      this.scene.background = new THREE.Color(0x818181)
      this.scene.fog = new THREE.FogExp2(0x818181, 0.0013)

      const loader = new GLTFLoader()
      loader.load('data/tv.gltf', (gltf) => {
        this.model = gltf.scene
        this.model.scale.set(0.5, 0.5, 0.5)
        this.scene.add(this.model)

        this.zoomAuto(this.model, this.camera)

        this.mixer = new THREE.AnimationMixer(this.model)
        const clips = gltf.animations
        const clip = THREE.AnimationClip.findByName(clips, 'drama')
        this.action = this.mixer.clipAction(clip)
        // this.action.play()
      })

      const geometry = new THREE.PlaneGeometry(2000, 2000)
      const material = new THREE.MeshStandardMaterial({
        side: THREE.DoubleSide,
        color: 0xffffff
      })
      const plane = new THREE.Mesh(geometry, material)
      this.scene.add(plane)
      plane.rotation.x = Math.PI * 0.5

      const grid = new THREE.GridHelper(2000, 20)
      this.scene.add(grid)

      this.ambient = new THREE.DirectionalLight({ color: 0x000000, intensity: 1 })
      this.scene.add(this.ambient)

      this.spot = new THREE.SpotLight(0xffffff, 5)
      this.spot.angle = 0.2
      this.scene.add(this.spot)
      this.scene.add(this.spot.target)
      this.spot.position.set(0, 1000, -300)

      this.renderer = new THREE.WebGLRenderer({ antialias: true })
      this.renderer.setPixelRatio(window.devicePixelRatio)
      this.renderer.outputEncoding = THREE.sRGBEncoding
      this.renderer.setSize(container.clientWidth, container.clientHeight)
      container.appendChild(this.renderer.domElement)

      this.controls = new OrbitControls(this.camera, this.renderer.domElement)
      this.resize()
    },
    animate (time) {
      time *= 0.001
      requestAnimationFrame(this.animate)
      this.renderer.render(this.scene, this.camera)
      this.controls.update()
      this.resize()
      if (this.mixer) {
        const xTime = time - this.previousTime
        this.mixer.update(xTime)
      }
      this.previousTime = time
    },
    resize () {
      const container = document.querySelector('.background')
      const width = container.clientWidth
      const height = container.clientHeight
      this.camera.aspect = width / height
      this.camera.updateProjectionMatrix()
      this.renderer.setSize(width, height)
    },
    zoomAuto (gltf, camera) {
      const box = new THREE.Box3().setFromObject(gltf)
      const sizeBox = box.getSize(new THREE.Vector3()).length()
      const centerBox = box.getCenter(new THREE.Vector3())
      const halfSizeModel = sizeBox * 0.5
      const halffov = THREE.MathUtils.degToRad(camera.fov * 0.5)
      const distance = halfSizeModel / Math.tan(halffov)
      const direction = (new THREE.Vector3()).subVectors(camera.position, centerBox).multiply(new THREE.Vector3(3, 0, 13)).normalize()
      const position = direction.multiplyScalar(distance).add(centerBox)
      camera.position.copy(position)
      this.controls.target.copy(centerBox)
      this.ambient.position.copy(position)
      this.spot.target.position.copy(centerBox)
      camera.near = sizeBox / 100
      camera.far = sizeBox * 100
      camera.updateProjectionMatrix()
      camera.lookAt(centerBox.x, centerBox.y, centerBox.z)
    },
    gsmoveOne () {
      const tl = gsap.timeline()
      tl.to(this.camera.position, {
        x: -336,
        y: 130,
        z: -592,
        duration: 1.5
      })
        .to(this.action.stop(), {})
    },
    gsmoveTwo () {
      const tl = gsap.timeline()
      tl.to(this.camera.position, {
        x: 0,
        z: -900,
        duration: 1.5
      })
        .to(this.action.play(), {})
    },
    gsmoveThree () {
      const tl = gsap.timeline()
      tl.to(this.camera.position, {
        x: 200,
        z: -300,
        duration: 1.5,
        onUpdate: function () {
        }
      })
    },
    gsmoveFour () {
      const tl = gsap.timeline()
      tl.to(this.camera.position, {
        y: 200,
        z: -500,
        duration: 1.5
      })
    }
  },
  mounted () {
    this.init()
    this.animate()
  }
}
</script>
