<template>
  <div id="intro">
    <h2 class="intxt">LIM JEOUNGGYU</h2>
    <h2 class="outtxt">PortFolio</h2>
  </div>
  <BackGround ref="bgd"></BackGround>
  <div class="frame">
    <div class="frame-top"></div>
    <div class="frame-left"></div>
    <div class="frame-right"></div>
    <div class="frame-bottom"></div>
  </div>
  <div class="header">
    <h2>LIM<br>JEOUNGGYU</h2>
    <span>Front End & Publisher</span>
    <ul class="list" id="list">
      <li @click="cameraMovesOne">
        <div v-if="isHome" class="squere"></div>
        <router-link v-else @click="showHome" to="/">HOME</router-link>
      </li>
      <li @click="cameraMovesTwo">
        <div v-if="isPro" class="squere"></div>
        <router-link v-else @click="showPro" to="/projects">PROJECTS</router-link>
      </li>
      <li @click="cameraMovesThree">
        <div v-if="isInfo" class="squere"></div>
        <router-link v-else @click="showInfo" to="/info">INFO</router-link>
      </li>
      <li @click="cameraMovesFour">
        <div v-if="isCon" class="squere"></div>
        <router-link v-else @click="showCon" to="/contact">CONTACT</router-link>
      </li>
    </ul>
  </div>
  <div class="pages">

    <router-view v-slot="{Component}">

      <transition name="page" mode="out-in">

        <component :is="Component"></component>

      </transition>

    </router-view>

  </div>

</template>

<script>
import BackGround from './components/BackGround.vue'

export default {
  name: 'App',
  components: { BackGround },
  data () {
    return {
      isHome: true,
      isPro: false,
      isInfo: false,
      isCon: false
    }
  },
  methods: {
    showHome () {
      if (!this.isHome) {
        this.isHome = true
        this.isPro = false
        this.isInfo = false
        this.isCon = false
      }
    },
    showPro () {
      if (!this.isPro) {
        this.isPro = true
        this.isHome = false
        this.isInfo = false
        this.isCon = false
      }
    },
    showInfo () {
      if (!this.isInfo) {
        this.isInfo = true
        this.isPro = false
        this.isHome = false
        this.isCon = false
      }
    },
    showCon () {
      if (!this.isCon) {
        this.isCon = true
        this.isPro = false
        this.isInfo = false
        this.isHome = false
      }
    },
    intros () {
      const int = document.getElementById('intro')
      const txt1 = int.querySelector('.intxt')
      const txt2 = int.querySelector('.outtxt')
      setTimeout(() => {
        txt1.style.opacity = 0
        txt1.style.transition = 'all 1.5s'
        txt2.style.opacity = 0
        txt2.style.transition = 'all 1.5s 0.3s'
      }, 1000)
      const removeInt = setInterval(() => {
        if (txt2.style.opacity <= 0) {
          int.style.opacity = 0
          int.style.transition = 'all 1s'
          int.style.zIndex = -1
          if (int.style.zIndex < 0) {
            setTimeout(() => {
              int.remove()
            }, 5000)
            clearInterval(removeInt)
          }
        }
      }, 3000)
    },
    cameraMovesOne () {
      this.$refs.bgd.gsmoveOne()
    },
    cameraMovesTwo () {
      this.$refs.bgd.gsmoveTwo()
    },
    cameraMovesThree () {
      this.$refs.bgd.gsmoveThree()
    },
    cameraMovesFour () {
      this.$refs.bgd.gsmoveFour()
    }
  },
  mounted () {
    this.intros()
  }
}
</script>

<style lang="scss">
@import "@/assets/main.scss";
@import "@/assets/reset.scss";
</style>

<style scoped lang="scss">
 .squere{
  width:10px;
  height:10px;
  background-color: #000;
 }
 .v-enter-active,.v-leave-active{
  transition: 0.1s;
 }
 .v-enter,.v-leave-to{
  opacity: 0;
 }
</style>
